.shop {
    grid-row: content-s/content-e;
    grid-column: s/e;
    

    display: grid;
    grid-template-rows: 30px [search-bar-s] 38px [search-bar-e] 57px [products-s] 1fr [products-e];
    grid-template-columns: [s] minmax(100px, 1035px) [e];
    justify-content: center;
    // gap: 57px;

    &__search-bar {
        grid-row: search-bar-s/search-bar-e;
        grid-column: s/e;
    }

    &__products {
        grid-row: products-s/products-e;
        display: grid;
        grid-column: s/e;
        grid-template-columns: repeat(auto-fill, minmax(240px, 1fr));
        column-gap: 25px;
        row-gap: 40px;
        // max-width: 1035px;
        width: 100%;
        justify-self: center;
        
    }

    &__cart-button {
        z-index: 5;
    }


    &__cart,
    &__cart-button {
        // grid-row: 1/-1;
        // grid-column: 1/-1;
        // align-self: start;
        // justify-self: end;

        

        position: absolute;
        top: 115px;
        right: 0px;
    }
}